#menu-gallery-menu {
	width: 100%;
	margin: 0;
	padding: 0;

	@include media-breakpoint-up('xl') {
		display: flex;
		justify-content: center;

		a { width: auto; }
	}
}

.ngg-figure {
	&:hover,
	&:focus {
		figcaption {
			background-color: $color-primary;

			h6 {
				font-weight: 600;
				font-size: 20px;
			} 
		}
	}
}

.ngg-pro-album.nextgen_pro_grid_album {
	position: relative;

	.nextgen_pro_lightbox,
	.image_link_wrapper { height: auto !important; }

	.image_container {
		width: auto !important;
	}

	.image_link_wrapper { margin-bottom: $spacer-base * 1.5; }
}

.image_container {
	margin-bottom: 0 !important;
	margin-left: 28px !important;

	.clear { display: none; } // remove the <br> tags..................

	.image_link_wrapper {
		.nextgen_pro_lightbox {
			position: relative;
			width: auto;
		}

		.caption_link {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			margin: auto;
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			transition: background 0.3s ease;
			cursor: pointer;

			a {
				color: $white;
				font-weight: 600;
				opacity: 0;
				transition: opacity 0.3s ease;
			}

			&:hover,
			&:focus {
				text-decoration: none;
				background-color: $green;

				a {
					text-decoration: none;
					opacity: 1;
				}
			}
		}
	}

	@include media-breakpoint-down('xl') {
		margin-left: 15px !important;
	}

	// Make the thing responsive...
	@include media-breakpoint-down('md') {
		width: auto !important;
		margin-left: 0 !important;
		margin-bottom: 28px !important;

		.image_link_wrapper,
		.nextgen_pro_lightbox {
			height: auto !important;
		}
	}
}

.ngg-trigger-buttons,
.galleria-nextgen-buttons { display: none; }

.galleria-info-title { display: none !important; }

// override carousel styles so that the image maintains it's original aspect ratio
.galleria-thumbnails-list {
	.galleria-image {
		width: auto !important;

		img {
			top: initial !important;
			height: 100% !important;
			width: auto !important;
			position: initial !important;
		}
	}
}

#npl_wrapper {
	padding-bottom: $spacer-base * 2;
}

.galleria-container {
	&.npl-thumbnails-open {
		.galleria-dock-toggle-container {
			bottom: 62px !important;
		}
	}

	.galleria-dock-toggle-container {
		bottom: 13px !important;
		transition: bottom 0.3s ease;
		opacity: 1 !important;
		background: $transparent !important;
	}

	.galleria-dock-toggle-container { opacity: 0.8 !important; }

	svg {
		font-size: 22px;
	}

	.galleria-thumb-nav-right { right: 73px !important; }

	.galleria-thumb-nav-left,
	.galleria-thumb-nav-right { margin-top: 12px !important; }

	.galleria-info {
		opacity: 1 !important;
	}

	.galleria-thumbnails-container,
	.galleria-info {
		padding-left: 85px;
		padding-right: 85px;
	}

	.galleria-thumbnails img { border-radius: 4px; }
}

.galleria-image-nav {
	svg {
		font-size: 52px !important;
		color: $silver !important;
	}

	.galleria-image-nav-right,
	.galleria-image-nav-left { opacity: 1 !important; }
}
