.learn-slider {
	margin-bottom: $spacer-section-lg;
	position: relative;
	max-width: 1400px;
	margin: 0 auto;

	.owl-stage-outer {
		&::before,
		&::after {
			content: '';
			display: block;
			width: 115px;
			max-width: 10%;
			position: absolute;
			height: 100%;
			top: 0;
			bottom: 0;
			margin: auto;
			z-index: 10;
		}

		&::before {
			left: 0;
			background: linear-gradient(270deg, rgba(255, 255, 255, 0) 0%, #fff 100%);
		}

		&::after {
			right: 0;
			background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, #fff 100%);
		}
	}

	.owl-carousel { position: relative; }

	.owl-nav {
		color: $silver;
		height: 0;
		width: 0;

		svg {
			width: 18.11px;
			height: 32px;
		}

		.owl-prev,
		.owl-next {
			position: absolute;
			padding: $spacer-base * 2;
			top: calc(50% - 30px);
		}
	}

	.owl-prev { left: 0px; }
	.owl-next { right: 0px; }

	.btn-wrapper { padding-top: $spacer-article; }

	.owl-stage {
		&::after { display: none; }
	}

	.learn-slide {
		// margin: 0 ($spacer-article * 0.5);

		&,
		img { width: 211px; }

		img {
			border: 1px solid $gray-200;
		}
	}

	@include media-breakpoint-up('sm') {
		.owl-item {
			width: auto !important;
		}
	}
}
