footer.site-footer {
	padding: $spacer-section 0 $spacer-article;

	&,
	a { color: $white; }

	ul {
		list-style: none;
		padding: 0;
		margin: 0;
	}

	h5 { color: $white; }

	.site-contact-info {
		font-size: 14px;

		p {
			margin-bottom: $spacer-base * 0.5;
		}
	}

	.social-nav-footer {
		margin-bottom: $spacer-article;

		svg {
			width: 18px;
			max-height: 18px;
			margin-right: $spacer-base * 2.5;
			transition: color 0.3s ease;

			&:hover,
			&:focus { color: $green; }
		}
	}

	#menu-footer-menu {
		display: flex;

		> li {
			margin-top: $spacer;
		}

		> li > a {
			font-size: 14px;
			text-transform: uppercase;
			color: rgba(255, 255, 255, 0.5);
			letter-spacing: 2px;
			line-height: 2.5;
			font-weight: 400;
		}

		> li > ul a {
			font-size: 16px;
			font-weight: 400;
			line-height: 32px;
		}
	}

	.site-footer-container-inner {
		max-width: 970px;
		margin: auto;
	}

	.site-footer-top {
		padding-bottom: $spacer-article;
	}

	.site-footer-bottom {
		padding-top: 20px;
		border-top: 1px solid rgba(255, 255, 255, 0.2);
		font-size: 12px;
	}

	@include media-breakpoint-up('lg') {
		.nav-footer {
			flex-grow: 2;
			padding-left: 20%;
		}

		#menu-footer-menu > li {
			width: 33.333333333%;
			margin-top: 0;
		}
	}
}
