#main > #tour-info {
	padding-top: $spacer-section-lg;
	padding-bottom: $spacer-article;
	background-color: #293335;
	position: relative;
	overflow: hidden;

	.tour-meta {
		position: relative;
		z-index: 2;
	}

	#tour-info-bg {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		margin: auto;
		z-index: 1;
		background-repeat: no-repeat;
		background-size: 125%;
		background-position: center 45px;
		opacity: 0.35;
	}

	&,
	h2 { color: $white; }

	img {
		margin: ($spacer-base * 5) auto 0;
		display: block;
		border-radius: 4px;
		box-shadow: 0 30px 40px 0 rgba(28, 30, 35, 0.4);
	}

	.tour-description {
		margin-bottom: $spacer-base * 5;
		// margin-top: $spacer-base * 5;
		min-height: 250px;
	}

	.tour-date-price {
		font-weight: 600;

		p {
			margin: 0 0 -8px;

			&:last-of-type { margin-bottom: $spacer-base * 2; }
		}
	}

	@include media-breakpoint-up('sm') {
		#tour-info-bg {
			top: 0%;
			background-position: center top;
			background-size: 85%;
		}

		.tour-description { min-height: 200px; }
	}

	@include media-breakpoint-up('md') {
		.tour-description { min-height: 100px; }
	}

	@include media-breakpoint-up('lg') {
		padding-bottom: $spacer-section-lg;
		margin-bottom: $spacer-base * 11.5;
		overflow: visible;

		.container {
			display: flex;
			align-items: stretch;
		}

		#tour-info-bg {
			position: absolute;
			width: 50%;
			left: initial;
			top: 0;
			bottom: 0;
			right: initial;
			background-position: center center;
			background-size: contain;
			opacity: 1;
			max-width: map-get($container-max-widths, 'xl') / 2;
		}

		.tour-meta {
			position: relative;
			max-width: 40%;
			margin-left: auto;

			img {
				margin: 0 0 (- $spacer-base * 12) -35%;
			}
		}
	}
}

#meet-the-guides {
	h2 + p { margin-top: -8px; }

	.guides {
		margin-bottom: $spacer-section - ($spacer-base * 3.5);
		margin-left: -$spacer-base * 3.5;
	}

	.guide {
		margin: 0 ($spacer-base * 3.5) ($spacer-base * 3.5);

		img {
			border-radius: 100px;
			width: 166px;
			margin-bottom: $spacer-base * 2;
		}
	}

	@include media-breakpoint-up('lg') {
		.guides { margin-bottom: 0; }
	}

	@include media-breakpoint-up('xl') {
		margin-right: 140px;
	}
}

#itenerary {
	margin-bottom: $spacer-section-lg;

	h2 + p {
		margin-top: -8px;
		font-size: $font-size-sm;
	}

	article {
		font-size: $font-size-sm;
		border-bottom: 1px solid $gray-300;
		margin-bottom: 1.15625 * $font-size-base; //18.5px
		padding-left: $spacer-base;
		padding-right: $spacer-base;

		&:last-of-type { border-bottom: none; }

		.badge { margin-bottom: 10px; }
		p { margin-left: $spacer-base; }
	}
}

.form-tour-contact {
	margin-top: $spacer-article;

	.nf-form-fields-required { text-align: left; }

	label,
	.nf-field-label label {
		font-weight: 400;
	}

	textarea { max-height: 120px; }
}

.tour-media-slider {
	figure {
		position: relative;
		margin: 0;
	}

	figcaption {
		position: absolute;
		left: $spacer-article;
		bottom: $spacer-base * 2;
		color: $white;
	}

	.owl-item img {
		// height: 680px;
		height: 300px;
		max-height: 80vh;
		width: auto;
		object-fit: contain;
		margin-left: auto;
		margin-right: auto;
	}

	.owl-carousel { position: relative; }

	.owl-nav {
		color: $silver;
		height: 0;
		width: 0;

		svg {
			width: 18.11px;
			height: 32px;
		}

		.owl-prev,
		.owl-next {
			position: absolute;
			padding: $spacer-base * 2;
			top: calc(50% - 16px);
		}
	}

	.owl-prev { left: 10px; }
	.owl-next { right: 10px; }

	@include media-breakpoint-up('sm') {
		.owl-prev { left: 40px; }
		.owl-next { right: 40px; }

		.owl-item img { height: 400px; }
	}

	@include media-breakpoint-up('md') {
		.owl-prev { left: 60px; }
		.owl-next { right: 60px; }

		.owl-item img { height: 500px; }
	}

	@include media-breakpoint-up('lg') {
		.owl-item img { height: 680px; }
	}

	@include media-breakpoint-up('xl') {
		.owl-item img { height: 780px; }
	}
}

.success-stories-section {
	h2 {
		padding-bottom: $spacer-section-lg;
	}

	.success-story-post-multiple {
		font-size: $font-size-sm;
		margin-bottom: $spacer-article;

		img {
			border-radius: 100px;
			margin-right: $spacer-base * 2;
		}

		.post-meta {
			span:first-of-type {
				color: $gray-800;
				margin-bottom: $spacer-base * 0.5;
			}
		}

		> p {
			margin-top: $spacer-base;
		}
	}

	@include media-breakpoint-up('md') {
		.success-story-post-multiple {
			margin-bottom: 0;
			margin-right: 40px;

			&:last-of-type { margin-right: 0; }
		}
	}

	@include media-breakpoint-up('xl') {
		.success-stories-wrapper {
			margin-left: $spacer-section;
			margin-right: $spacer-section;
		}

		.success-story-post-multiple {
			margin: 0 40px;
		}
	}
}

.main .tour-signup {
	background-color: $gray-800;
	padding-top: $spacer-section-lg;
	padding-bottom: $spacer-section-lg;
	font-size: $font-size-base;

	&,
	h2 { color: $white; }

	.section-wrapper {
		max-width: 870px;
		margin: 0 auto;
	}
}
