.nf-error-msg:not(.nf-error-field-errors) {
	background-color: #e80000;
	color: $white !important;
	font-size: $font-size-xs;
	margin-right: 0 !important;
	padding-left: $spacer-base;
}

.nf-input-limit + .nf-error {
	margin-top: -6px;
}

.textarea-container .nf-input-limit + .nf-error {
	margin-top: -13px;
}

.nf-error-msg.nf-error-field-errors {
	width: 100%;
	margin: $spacer-article 0;
	background-color: #e80000;
	color: white;
	font-size: $font-size-sm;
	padding: $spacer-base;
}
