.sidebar-social-share {
	display: flex;
	margin-bottom: $spacer-base;

	> a {
		margin-right: 8px;
		width: calc(33.333333333% - #{16px/3});
		display: flex;
		justify-content: center;
		align-items: center;
		color: $white;
		padding: $spacer-base * 1.4;
		border-radius: 4px;
		transition: background-color 0.3s ease;

		&:last-of-type { margin-right: 0; }

		&:hover,
		&:focus { color: $white !important; }
	}

	.facebook {
		background-color: $color-facebook;

		&:hover,
		&:focus {	background-color: darken($color-facebook, 10%); }
	}

	.twitter {
		background-color: $color-twitter;

		&:hover,
		&:focus {	background-color: darken($color-twitter, 10%); }
	}

	.mail {
		background-color: $teal;

		&:hover,
		&:focus {	background-color: darken($teal, 10%); }
	}
}
