.loader-wrapper {
	width: 100%;
	padding: 60px;
	display: flex;
	justify-content: center;
	opacity: 0;
	transition: all 0.15s ease;

	.loader {
		border: 16px solid $gray-300; /* Light grey */
		border-top: 16px solid theme-colors('primary'); /* Blue */
		border-radius: 50%;
		animation: spin 2s linear infinite;
		width: 0;
		height: 0;
	}

	&.active {
		opacity: 1;

		.loader {
			width: 120px;
			height: 120px;
		}
	}

	@keyframes spin {
		0% { transform: rotate(0deg); }
		100% { transform: rotate(360deg); }
	}
}
