.tours-slider {
	max-width: 1400px;
	margin: 0 auto;
	position: relative;

	article {
		&,
		h2,
		a {
			color: $white;

			&:hover,
			&:focus {
				text-decoration: none;
				color: $white;
			}
		}

		> a {
			width: 100%;
			background-size: cover;
			background-position: center center;
			background-repeat: no-repeat;
			display: flex;
			align-items: flex-end;
			height: 100vh;
			max-height: 478px;
		}
	}

	.tour-meta {
		padding: $spacer-article ($spacer-article * 1.5) $spacer-article ($spacer-article * 0.5);

		p {
			line-height: 1.5;
			margin: 0;
		}
	}

	.btn { margin-top: $spacer-article; }

	.owl-dots {
		position: absolute;
		top: 0;
		bottom: 0;
		right: $spacer-article * 0.5;
		display: flex;
		flex-direction: column;
		justify-content: center;

		.owl-dot {
			height: 9.06px;
			width: 9.06px;
			display: block;
			background-color: $white;
			border-radius: 100px;
			margin: 17px ($spacer-base * 0.5);
			border: 1px solid $transparent;
			position: relative;

			&.active {
				border-color: $green;
				background-color: $green;

				&::before {
					content: '';
					width: 19px;
					height: 19px;
					border: 1px solid $green;
					display: block;
					border-radius: 100px;
					margin-left: -6px;
					margin-top: -6px;
				}
			}
		}
	}

	@include media-breakpoint-up('lg') {
		article > a {
			height: 640px;
			max-height: 640px;
		}

		.tour-meta { padding-left: $spacer-article; }
	}
}
