.search-modal-toggle {
	border: none;
	background-color: rgba(0, 0, 0, 0);
	cursor: pointer;

	&:hover,
	&:focus { outline: none; }
}

.search-modal {
	position: fixed;
	z-index: 999;
	width: 100%;
	left: 0;
	top: 0;
	height: 100vh;
	background-color: rgba(255, 255, 255, 0.9);

	.search-modal-toggle {
		position: absolute;
		top: $spacer-article;
		right: $spacer-article;
	}

	form {
		padding-top: 25vh;
		width: 830px;
		max-width: 90%;

		.input-group {
			width: 100%;
			position: relative;
		}

		button {
			border: 0;
			background: rgba(255, 255, 255, 0);
			position: absolute;
			left: 5px;
			top: 0;
			bottom: 0;
			z-index: 2;

			svg.fa-search {
				width: 32px;
				height: 32px;
				margin-top: $spacer-base;
				margin-left: 10px;
			}
		}

		input {
			font-size: 18px;
			padding: 24px 72px;
			border: none;
			border: 1px solid $gray-200;
			background-color: $white;
			width: 100%;
			position: relative;
			z-index: 1;

			&:focus,
			&:hover {
				outline-width: 1px;
				outline-color: $green;
			}
		}
	}
}

.main .search-modal-toggle {
	color: $color-primary;
	text-decoration: underline;
	padding: 0;
	display: inline;
}

.posts-search {
	.post-search {
		margin-bottom: $spacer-article;

		h5 {
			color: $gray-800;
			margin-bottom: $spacer-base * 0.5;
			margin-top: $spacer-article * 0.75;
		}

		h3 {
			margin-bottom: $spacer-base * 1.65;

			a {
				color: $gray-800;

				&:hover,
				&:focus {
					text-decoration: none;
					color: $color-primary;
				}
			}
		}

		img {
			min-width: 270px;
			max-width: 270px;
			height: 180px;
			object-fit: cover;
			margin-right: $spacer-article;
		}

		p {
			font-size: $font-size-base;
			line-height: 24px;

			&.read-more { padding-top: 8px; }
		}

		&.post-type-learn {
			img {
				min-width: 180px;
				max-width: 180px;
			}
		}
	}

	@include media-breakpoint-up('md') {
		.post-search h5 { margin-top: 0; }
	}
}
