#menu-main-menu a { padding-bottom: $spacer-base * 2; }

#menu-main-desktop-wrapper {
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
	top: 0;
	margin: auto;
	display: flex;
	justify-content: center;
	align-items: center;
	margin-left: 260px; // length of logo
	margin-right: 144px; // length of social-nav
}

#menu-main-desktop {
	font-size: 13px;

	a {
		transition: 0.3s ease;
		color: $black;
		text-transform: uppercase;
		letter-spacing: 1px;
		margin-right: 30px;
		border-bottom: 2px solid rgba(0, 0, 0, 0);
		padding: ($spacer-base * 0.25) 0;

		&:hover,
		&:focus {
			border-color: $green;
		}
	}

	li:last-of-type a { margin-right: 0; }
}
