.posts-learn {
	.gutter-sizer { width: 5%; }

	.post-learn-grid-item {
		width: 47.5%;
		margin-bottom: 5%;
	}

	@include media-breakpoint-up('md') {
		.gutter-sizer { width: 4%; }

		.post-learn-grid-item {
			width: 30.666666667%;
			margin-bottom: 4%;
		}
	}

	@include media-breakpoint-up('lg') {
		.gutter-sizer { width: 2%; }

		.post-learn-grid-item {
			width: 18.4%;
			margin-bottom: 2%;
		}
	}
}

.posts-learn-grid {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}

.post-learn {
	position: relative;
	transition: box-shadow 0.3s ease;

	img {
		max-width: 100%;
		border: 1px solid $gray-200;
		position: relative;
		z-index: 1;
	}

	.content {
		position: absolute;
		display: flex;
		top: 0;
		bottom: 0;
		margin: auto;
		z-index: 2;
		width: 100%;
		text-align: center;
		justify-content: center;
		align-items: center;
		opacity: 0;
		transition: all 0.3s ease;

		.btn-wrapper {
			width: 100%;
		}

		.btn {
			padding-left: 0;
			padding-right: 0;
			width: 187px;
			max-width: 90%;
		}
	}

	&:hover,
	&:focus {
		box-shadow: 0 30px 40px 0 rgba(28, 30, 35, 0.4);

		.content { opacity: 1; }

		.btn {
			background-color: $green;
			border-color: $green;
		}
	}
}
