#main .breadcrumbs-wrapper {
	background-color: $gray-900;
	margin-bottom: -$spacer-section-lg;
	margin-top: -$spacer-section-lg;
	display: flex;

	a {
		font-size: $font-size-xs;
		letter-spacing: 1px;
		text-transform: uppercase;
		color: $white;
		padding: $spacer-base * 2;
		display: flex;
		justify-content: space-between;
		align-items: center;
		transition: background 0.3s ease;

		&:hover,
		&:focus {
			text-decoration: none;
			background-color: $black;
		}
	}

	.previous-post svg {
		margin-right: $spacer-base;
	}

	.next-post {
		text-align: right;

		svg { margin-left: $spacer-base; }
	}

	> div {
		width: 50%;

		&:first-of-type,
		&.index-posts { border-right: 1px solid $white; }
	}

	&.breadcrumbs-3-part {
		> div:first-of-type,
		> div:last-of-type {
			width: calc(50% - (186px / 2));
		}

		> div.index-posts {
			width: 186px;
			display: flex;

			a {
				width: 100%;
				display: flex;
				justify-content: center;
				align-items: center;
			}
		}
	}
}

#main .post-related-posts + .breadcrumbs-wrapper { margin-top: 0; }
