.badge.badge-itenerary {
	background-color: $red;
	padding: 0 !important;

	> span {
		padding: 3px $spacer-base;
		display: inline-block;
		line-height: 1.5;
		text-transform: none;
		letter-spacing: 0;

		&:first-of-type {
			padding-left: $spacer-base;
			border-right: 2px solid rgba(255, 255, 255, 0.5);
		}
	}
}
