.success-story-post-single {
	margin-top: $spacer-article;
	margin-bottom: $spacer-article;
	flex-direction: column;
	border-left: 4px solid $green;
	padding-left: $spacer-base * 2;

	img {
		width: 45px;
		height: 45px;
		border-radius: 100px;
		margin-right: 17px;
	}

	.post-meta {
		font-size: $font-size-sm;
		white-space: nowrap;
	}

	> div:last-of-type {
		padding-top: $spacer-base * 2;
		font-family: $font-family-serif;
		font-style: italic;
		font-weight: 600;
		font-size: $font-size-base;
	}

	@include media-breakpoint-up('md') {
		flex-direction: row;
		padding-left: 0;
		border-left: none;

		> div:first-of-type {
			padding-right: $spacer-base * 2;
			border-right: 4px solid $green;
			flex-shrink: 0;
		}

		> div:last-of-type {
			padding-top: 0;
			padding-left: $spacer-base * 2;
			font-family: $font-family-serif;
			font-style: italic;
			font-weight: 600;
			font-size: $font-size-base;
		}
	}
}
