.flexible-post-image {
	img { width: 100%; }
}

.main section + section.flexible-post-quote {
	margin-top: - $spacer-section-lg;
}

.main .post-highlight {
	background-color: $green;
	padding: $spacer-article;
	font-weight: 600;

	p { line-height: 1.5; }
	p:last-of-type { margin-bottom: 0; }

	&,
	h5 { color: $white; }
}

.main .flexible-post-quote {
	text-align: center;
	border-bottom: 1px solid $gray-100;
	border-top: 1px solid $gray-100;
	margin-top: 0;
	margin-bottom: 0;
	padding-top: $spacer-section-lg;
	padding-bottom: $spacer-section-lg;

	+ section { margin-top: 0; }

	h2 {
		display: flex;
		align-items: flex-start;
		margin-bottom: $spacer-article;
	}

	.right-quote { align-self: flex-end; }

	span[class*="-quote"] { font-size: 54px; }

	.quote {
		padding-left: $spacer-article;
		padding-right: $spacer-article;
	}

	@include media-breakpoint-up('md') {
		.quote {
			padding-left: $spacer-section-lg;
			padding-right: $spacer-section-lg;
		}
	}
}

.post-author-section {
	.post-author-content {
		border-radius: 4px;
		margin-left: -15px;
		margin-right: -15px;

		> div {
			padding: $spacer-article;
		}
	}

	.post-tags-and-social {
		border: 1px solid $gray-100;
		border-top-left-radius: 4px;
		border-top-right-radius: 4px;
	}

	.post-author {
		border: 1px solid $gray-100;
		border-top-width: 0;
		border-bottom-left-radius: 4px;
		border-bottom-right-radius: 4px;
	}

	.post-tags {
		a {
			transition: background 0.3s ease;
			margin-right: $spacer-base;
			margin-bottom: $spacer-base;

			&:last-of-type { margin-right: 0; }
		}
	}

	.post-social-comment {
		color: $gray-500;
		font-size: $font-size-xs;
		display: inline-flex;
		align-items: center;

		svg {
			width: 14px;
			margin-right: 7px;
		}
	}

	.post-social-twitter,
	.post-social-facebook {
		margin-left: $spacer-base * 2;
		padding: ($spacer-base * 0.7) ($spacer-base * 1.5);
		color: $white;
		border-radius: 100px;
		min-width: 90px;
		display: inline-flex;
		justify-content: space-between;
		align-items: center;
		font-size: $font-size-sm;
		transition: background-color 0.3s ease;

		&:hover,
		&:focus {
			text-decoration: none;
			color: $white;
		}
	}

	.post-social-facebook {
		background-color: $color-facebook;

		svg {
			height: 17.99px;
			width: 9.02px;
		}

		&:hover,
		&:focus {
			background-color: darken($color-facebook, 10%);
		}
	}

	.post-social-twitter {
		background-color: $color-twitter;

		h5 {
			color: $white;
			margin: 0;
			margin-left: $spacer-base * 0.5;
		}

		svg {
			height: 14px;
			width: 17px;
		}

		&:hover,
		&:focus {
			background-color: darken($color-twitter, 10%);
		}
	}

	.post-author {
		.img-wrapper {
			max-width: 55px;
			margin-right: $spacer-base * 1.5;
		}

		.author-name {
			font-size: $font-size-sm;
			color: $gray-800;
		}

		.author-quote { font-size: $font-size-base; }

		img {
			min-width: 100%;
			height: 55px;
			object-fit: cover;
			border-radius: 100px;
		}
	}

	@include media-breakpoint-up('sm') {
		.post-author-content {
			max-width: 1029px;
			margin-left: auto;
			margin-right: auto;
		}
	}
}

.facebook-comment-plugin {
	.fb_iframe_widget {
		margin: 0 auto;
		display: block;

		> span {
			margin: 0 auto;
			display: block;
		}
	}

	iframe,
	.fb_iframe_widget,
	.fb_iframe_widget > span {
		max-width: 100%;
	}
}

.post-related-posts {
	overflow: hidden;
	margin-bottom: $spacer-section * 2;

	article {
		margin-right: $spacer-article;
		margin-left: 0;
	}

	.container > h5 {
		border-bottom: 1px solid $gray-100;
		border-top: 1px solid $gray-100;
		padding: ($spacer-base * 2) 0;
		margin-bottom: 0;
		text-align: center;
	}

	.post-related-posts-inner-wrapper {
		margin: 0 (-$spacer-article) 0 0;
	}

	@include media-breakpoint-up('lg') {
		article {
			&:first-of-type { margin-top: $spacer-article; }
		}
	}

	@include media-breakpoint-up('xl') {
		article {
			width: calc(25% - #{$spacer-article * 0.75});
			max-width: calc(25% - #{$spacer-article});
		}
	}
}
