.post-blog-primary {
	position: relative;

	figcaption {
		position: absolute;
		bottom: 0;
		left: 0;
		background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, $black 100%);
		padding: 84px $spacer-article $spacer-article;

		&,
		h3,
		h5,
		a { color: $white; }
	}

	img {
		min-height: 480px;
		width: 100%;
		object-fit: cover;
	}

	p { line-height: 27px; }

	@include media-breakpoint-up('lg') {
		figcaption { padding-top: 134px; }
		img { min-height: 580px; }
	}
}

.main .subscribe-to-digest {
	background: $gray-800;
	background-size: cover;
	background-position: center center;
	padding: $spacer-section-lg;

	h2 { margin-bottom: 0; }
	p { font-size: $font-size-sm; }

	&,
	h2 { color: $white; }

	label { display: none; }

	#mc_embed_signup_scroll {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;

		input[type="email"] {
			$color: rgba(255, 255, 255, 0.15);

			color: $white;
			border: 1px solid $color;
			border-radius: 0;
			margin-right: $spacer-base * 0.5;
			margin-bottom: $spacer-base * 0.5;
			width: 370px;
			max-width: 80%;
			background-color: $color;
		}


		input[type="submit"] {
			@extend .btn;
			@extend .btn-primary;

			border: none;
			margin-bottom: $spacer-base * 0.5;
		}

		> .clear {
			display: flex;
		}
	}

	@include media-breakpoint-up('md') {
		#mc_embed_signup_scroll {
			input[type="email"] { max-width: 100%; }

			input[type="email"],
			input[type="submit"] { margin-bottom: 0; }
		}
	}
}

.post-blog-nonprimary {
	margin-top: $spacer-article;
	margin-left: 0;
	margin-right: 0;

	img {
		min-height: 180px;
		width: 100%;
		object-fit: cover;
	}

	h5 {
		margin-top: $spacer-article * 0.5;
		margin-bottom: 7px;

		a {
			color: $gray-800;
			transition: color 0.3s ease;

			&:hover,
			&:focus {
				text-decoration: none;
				color: $color-primary;
			}
		}
	}

	h3 {
		margin-top: $spacer-base * 0.75;

		&,
		a {
			color: $gray-800;

			&:hover,
			&:focus {
				text-decoration: none;
				color: $color-primary;
			}
		}
	}

	@include media-breakpoint-up('sm') {
		max-width: auto;
		margin-left: 0;
		margin-right: 0;
	}

	@include media-breakpoint-up('lg') {
		&:first-of-type { margin-top: 0; }
	}
}

.posts-blog-main {
	.col-lg-3 {
		display: flex;
		flex-wrap: wrap;
	}

	@include media-breakpoint-up('sm') {
		.col-lg-3 {
			flex-wrap: nowrap;
			justify-content: center;

			article {
				&:first-of-type { margin-right: $spacer-article * 0.5; }
				&:last-of-type { margin-left: $spacer-article * 0.5; }
			}
		}
	}

	@include media-breakpoint-up('lg') {
		.col-lg-3 {
			display: block;
			justify-content: flex-start;

			article {
				margin-left: 0 !important;
				margin-right: 0 !important;
			}
		}
	}
}

.posts-tertiary-wrapper {
	display: flex;
	flex-wrap: wrap;
	margin-top: -$spacer-article;

	article {
		width: 100%;
		margin-top: $spacer-article;
	}

	@include media-breakpoint-up('sm') {
		margin-right: -$spacer-article;

		article {
			width: calc(50% - #{$spacer-article});
			margin-right: $spacer-article;
		}
	}

	@include media-breakpoint-up('md') {
		article { width: calc(33.333333333% - #{$spacer-article}); }
	}

	@include media-breakpoint-up('lg') {
		article {
			width: calc(25% - #{$spacer-article});
			&:first-of-type { margin-top: $spacer-article; }
		}
	}
}

.posts-blog-home {
	padding: ($spacer-base * 1.5) 0;

	@include media-breakpoint-up('sm') {
		.col-lg-6:last-of-type {
			display: flex;
			flex-wrap: wrap;

			article {
				width: calc(50% - #{$spacer-article * 0.5});

				&:nth-of-type(odd) { margin-right: $spacer-article * 0.5; }
				&:nth-of-type(even) { margin-left: $spacer-article * 0.5; }
			}
		}
	}

	@include media-breakpoint-up('md') {
		.col-lg-6:last-of-type {
			justify-content: center;
		}
	}

	@include media-breakpoint-up('lg') {
		.col-lg-6:last-of-type {
			justify-content: flex-start;

			article {
				&:nth-of-type(n + 2) { margin-top: 0; }
			}
		}
	}
}
