.sidebar-form {
	.sidebar-form-inner-wrapper {
		// margin: $spacer-article auto 0;
		width: 100%;
		border-radius: 4px;
		background-color: $red;
		padding-bottom: $spacer-article;

		&,
		h5 { color: $white; }

		h5 {
			padding: ($spacer-base * 1.5) ($spacer-base * 2);
			border-bottom: 1px solid $red-dark;
			text-align: center;
			margin-bottom: $spacer-article;
		}

		.sidebar-form-title,
		.sidebar-form-form {
			padding-left: $spacer-article;
			padding-right: $spacer-article;
		}

		.sidebar-form-form p { display: none; }

		.sidebar-form-title {
			display: flex;
			flex-direction: column;
			align-items: center;
			text-align: center;

			svg {
				width: 37.01px;
				height: 36px;
				margin-bottom: $spacer-base;
			}

			h3 {
				color: $white;
				font-size: 1.25rem;
				line-height: 26px;
				font-weight: 400;
				font-family: $font-family-sans-serif;
				margin-bottom: $spacer-article;
				width: 100%;
			}
		}

		.sidebar-form-form {
			label,
			h2 { display: none; }

			input[type="email"] {
				background-color: $red-dark;
				width: 100%;
				padding: $spacer-base ($spacer-base * 1.8);
				border: none;
				color: $white;
			}

			input { margin-bottom: $spacer-base; }
			input[type="submit"] { margin-bottom: 0; }

			input[type="submit"] {
				@extend .btn;

				background-color: $transparent;
				border-color: $white;
				color: $white;
				border-width: 2px;
				padding-top: $spacer-base * 1.2;
				padding-bottom: $spacer-base * 1.3;
				font-size: $font-size-xs;
				width: 100%;
				display: flex;
				justify-content: center;

				&:hover,
				&:focus {
					background-color: $red-dark;
				}
			}

			::-webkit-input-placeholder,
			::-moz-placeholder,
			:-ms-input-placeholder,
			:-moz-placeholder
			::placeholder { color: $white; }
		}
	}
}
