#menu-social,
#menu-social-desktop {
	list-style: none;
	padding: 0;
	margin: 0;

	svg {
		max-height: 16px;
		margin-right: $spacer-base * 2;
		width: 16px;
	}

	li {
		display: flex;
		justify-content: center;
		align-items: center;

		&:last-of-type svg { margin-right: 0; }
	}

	button {
		padding: 0;
		display: flex;
		align-items: center;
		justify-content: center;
	}
}

#menu-social-desktop {
	display: none !important;

	svg,
	button {
		color: $black;
		transition: color 0.3s ease;
	}

	a,
	button {
		&:hover svg,
		&:focus svg {
			color: $green;
		}
	}

	@include media-breakpoint-up('lg') {
		display: flex !important;
	}
}
