.nf-field .submit-wrap .nf-field-element {
	display: flex;
	justify-content: flex-end;
}

.select2-selection.select2-selection--single { height: auto; }

.nf-row {
	margin-left: -5px;
	margin-right: -5px;
}

.select2-results__option,
input,
textarea { transition: color 0.3s ease, background 0.3s ease, outline 0.3s ease; }

input[type="text"],
input[type="email"],
input[type="phone"],
input[type="tel"],
textarea,
.select2-selection.select2-selection--single {
	border: 1px solid $silver;
	border-radius: 2px;
}

input[type="text"],
input[type="email"],
input[type="phone"],
input[type="tel"],
textarea,
.select2-search__field {
	&:focus {
		outline-color: $color-primary;
		outline-width: 1px;
	}
}

input[type="text"],
input[type="email"],
input[type="phone"],
input[type="tel"],
textarea,
.select2-selection__rendered,
.select2-results__option {
	padding: $spacer-base;
	font-size: $font-size-sm;
	color: $gray-700;

	&,
	span { line-height: $line-height-base; }
}

.select2-selection__rendered,
.select2-results__option {
	padding: $spacer-base !important;

	&,
	span { line-height: $line-height-base !important; }
}

.select2-container--default .select2-results__option--highlighted[aria-selected] {
	background-color: $color-primary;
	color: $white;
}

.nf-form-fields-required,
.nf-field-label {
	margin-left: $spacer-base !important;
}

.nf-form-fields-required,
label {
	text-transform: uppercase;
	color: $gray-500;
	font-size: $font-size-xs;
	letter-spacing: 1px;
	font-weight: 400;
}

.nf-form-fields-required { font-weight: 400 !important; }

.nf-field-label { margin-bottom: $spacer-base * 0.5 !important; }

.select2-selection__arrow {
	top: 0 !important;
	bottom: 0;
	right: $spacer-base !important;
	margin: auto;
	display: flex;
	align-items: center;
	width: 10px !important;
}

$placeholder-color: $gray-500;

::-webkit-input-placeholder { color: $placeholder-color; }
::-moz-placeholder { color: $placeholder-color; }
:-ms-input-placeholder { color: $placeholder-color; }
:-moz-placeholder { color: $placeholder-color; }
::placeholder { color: $placeholder-color; }

form {
	#MERGE3,
	br { display: none; }
}
