.tours-content {
	margin-bottom: ($spacer-section-lg) - 1rem;

	@include media-breakpoint-up('lg') {
		column-count: 2;
		column-gap: $spacer-article;

		> * {
			display: inline;
			width: 100%;
		}
	}

	// multicolumn is calculating column heights strangley here, so adjusting the margin-bottom to accomodate
	@media (min-width: map-get($grid-breakpoints, 'lg')) and (max-width: map-get($grid-breakpoints, 'xl')) {
		margin-bottom: ($spacer-base * 4) - 1rem;
	}
}

.main .posts-tours-grid {
	margin-bottom: - $spacer-base * 5;

	img {
		height: 337.19px;
		min-width: 100%;
		object-fit: cover;
		border-radius: 4px;
		transition: box-shadow 0.3s ease;
	}

	.post-tours {
		position: relative;
		margin-bottom: $spacer-base * 5;

		&:hover,
		&:focus {
			img { box-shadow: 0 30px 40px 0 rgba(28, 30, 35, 0.4); }
		}

		figcaption {
			margin-top: $spacer-base;
			font-size: $font-size-base;

			h2 {
				line-height: 1.157894737;
				margin: 0;
				display: none;
			}
		}

		&.tag-color-transparent {
			.meta .meta-items > span {
				border-color: $white;
				background-color: $transparent;
			}
		}

		.meta {
			position: absolute;
			top: $spacer-base * 2;
			left: $spacer-base * 2;
			letter-spacing: 1px;

			a {
				box-shadow: 0 30px 40px 0 rgba(28, 30, 35, 0.4);

				&:hover,
				&:focus { text-decoration: none; }
			}

			.meta-items {
				display: flex;

				> span {
					display: inline-block;
					border-width: 2px;
					border-color: $green-dark;
					background-color: $green-light;
					color: $white;
					font-size: 11px;
					line-height: 1.545454545;
					text-transform: uppercase;
					transition: background 0.3s ease;
				}

				> span:first-of-type {
					padding: 13px $spacer-base 13px $spacer-article;
					border-style: solid;
					border-top-left-radius: 100px;
					border-bottom-left-radius: 100px;
				}

				> span:last-of-type {
					padding: 13px $spacer-article 13px $spacer-base;
					border-top-right-radius: 100px;
					border-bottom-right-radius: 100px;
					border-top-style: solid;
					border-bottom-style: solid;
					border-right-style: solid;
				}
			}
		}
	}

	@include media-breakpoint-up('md') {
		img { height: 218.18px; }

		.post-tours {
			width: calc(50% - #{$spacer-article / 2});

			&:nth-of-type(odd) { margin-right: $spacer-article * 0.99; } //0.99 b/c IE and Edge can't Math
		}
	}
	@include media-breakpoint-up('lg') {
		img { height: 297.52px; }
	}

	@include media-breakpoint-up('xl') {
		img { height: 400px; } //img max 600x400
	}
}

.success-stories-section {
	article { width: 100%; } //necessary for IE11
}

.main .form-tours-update-section {
	padding-top: $spacer-section-lg;
	padding-bottom: $spacer-section-lg;
	background-repeat: no-repeat;
	background-size: cover;

	&,
	h2 { color: $white; }

	> .container > div {
		max-width: 729px;
		margin: 0 auto;
	}

	h2 { margin-bottom: $spacer-base * 2; }

	label { display: none; }

	input {
		width: 100%;
		max-width: 270px;
		margin-bottom: $spacer-base;
		color: $white;

		&:last-of-type { margin-bottom: 0; }
	}

	input:not([type="submit"]) {
		background-color: #242b2c;
		border: none;
		text-align: center;
		padding: $spacer-base * 1.5;
	}

	input[type="submit"] {
		@extend .btn;
		@extend .btn-primary;

		margin-bottom: $spacer-base * 5;
		padding-top: $spacer-base * 1.6;
		padding-bottom: $spacer-base * 1.6;
	}

	.small {
		max-width: 420px;
		margin: 0 auto;
	}
}


