table {
	width: 100%;
	max-width: 100%;
	margin: $spacer-article 0;
}

thead {
	background-color: $gray-300;

	> tr:first-of-type > td:first-of-type { border-top-left-radius: $border-radius; }
	> tr:first-of-type > td:last-of-type { border-top-right-radius: $border-radius; }
}

tbody {
	> tr:last-of-type > td:first-of-type { border-bottom-left-radius: $border-radius; }
	> tr:last-of-type > td:last-of-type { border-bottom-right-radius: $border-radius; }
}

td, th {
	padding: 5px 10px;
}

tr {
	&:nth-of-type(2n + 2) {
		background-color: $gray-100;
	}
}
